.page {
  font-family: Roboto, sans-serif;
}

.page__body {
  margin: 0;
}

.page__main {
  padding: 50px 40px;
}

p {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  height: 40px;
  display: inline-block;
}

.nav__list {
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin: 0 10px;
  position: relative;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  text-decoration: none;
  transition: all .3s ease-in-out;
  display: flex;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link:hover, .nav__link[data-qa="nav-hover"]:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.products {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  font-weight: 400;
  display: grid;
}

@media (width >= 488px) {
  .products {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .products {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .products {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  padding: 32px 0 16px;
  transition: all .3s ease-in-out;
}

.card__image {
  margin-bottom: 40px;
  padding-inline: 19px;
  display: flex;
}

.card__image img {
  width: 160px;
  height: 134px;
}

.card__content {
  padding-inline: 16px;
}

.card__title {
  color: #060b35;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  margin-bottom: 24px;
}

.card__price {
  margin-bottom: 16px;
}

.card__link {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s ease-in-out;
  display: flex;
}

.card__link[data-qa="card-hover"]:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.rating {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.rating__reviews {
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

.price {
  justify-content: space-between;
  display: flex;
}

.price__text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.price__money {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star--1, .stars__star--2, .stars__star--3, .stars__star--4 {
  background-image: url("star-active.edda4c43.svg");
}

